/*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
 1. 项目名称：悟空找房移动端FE-MVC框架
 2. 页面名称：tabs (jQuery插件 - tabs)
 3. 作者：zhaohuagang@lifang.com
 4. 实例：
    $.tabs({       
        handleClass : "tabs-handle",        
        activeHandleClass : "on",        
        frameClass : "tabs-frame", 
        effect : "show" ,        
        duration : 0 ,        
        onSwap : null ,        
        fixedWhenScroll : true
    }) ;
 -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
(function($) {
    $.fn.tabs = function(options) {
        /*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
        将每个tabs加上对应的配置属性
        -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
        var opts = $.extend({}, $.fn.tabs.defaults, options);
        /*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
         执行事件添加后返回
         -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
        return this.each(function() {
            /*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
             tabs-handle子节点平均分配宽度
             -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
            //var $lis = $(this).find("." + opts.handleClass + ">li") ;
            //$lis.css({ "width" : 100 / $lis.size() + "%" }) ;
            /*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
             先隐藏掉activeIndex之外的其他frame及handle
             -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/             
            $.fn.tabs.swapTo(this, $(this).find("." + opts.handleClass + " ." + opts.activeHandleClass).index(), opts) ;
            /*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
             给handle的子元素赋予事件
             -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
            var tabs = this;
            $(this).find("." + opts.handleClass).children().each(function(key, element) {
                $(element).on("click", function() {
                    $.fn.tabs.swapTo(tabs, key, opts);                    
                });
            }) ;
            /*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
             窗口拉动滚动条的时候侦测滚动条位置，大于指定值的时候position变成fixed
             -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
            if(opts.fixedWhenScroll) {
                $(window).scroll(function(){
                    var $tabsHandle = $(tabs).find("." + opts.handleClass) ;
                    if($(window).scrollTop() >= $tabsHandle.height()) $tabsHandle.css({ "position" : "fixed" , "width" : "100%" , "left" : 0 , "top" : 0 }) ;
                    else $tabsHandle.css({ "position" : "static"}) ;
                }) ;
            }
        });
    };
    /*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
     定义切换到某一帧的方法
     -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
    $.fn.tabs.swapTo = function(tabs, index, opts) {
        var $handles = $(tabs).find("." + opts.handleClass).children() ;
        $handles.removeClass(opts.activeHandleClass);
        $handles.eq(index).addClass(opts.activeHandleClass);
        $(tabs).find("." + opts.frameClass).hide();
        if(opts.duration == "normal" || opts.duration == "fast" || opts.duration == "slow") eval("$(tabs).find('.' + opts.frameClass).eq(index)." + opts.effect + "('" + opts.duration + "')");
        else eval("$(tabs).find('.' + opts.frameClass).eq(index)." + opts.effect + "(" + opts.duration + ")") ;
        /*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
        触发window的scroll事件，有些页面加了延迟加载图片插件
        -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
        $(window).trigger("scroll") ;
        /*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
        调用接口事件
        -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
        if(opts.onSwap) opts.onSwap(index) ;
    } ;
    /*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
     tabs默认配置
     -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
    $.fn.tabs.defaults = {
        /*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
         句柄容器的class名称
         -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
        handleClass : "tabs-handle",
        /*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
         活动标签页需要加的class名称
         -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
        activeHandleClass : "on",
        /*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
         每个标签卡内容的class名称
         -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
        frameClass : "tabs-frame",       
        /*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
         切换效果设置，效果暂时设置为：
         1. slideDown : 向下拉下来
         2. fadeIn : 通过透明度的切换显示
         3. show : 普通显示
         -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
        effect : "show" ,
        /*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
         切换动画持续时间，可以是整数，单位是毫秒，可以是：normal | fast | slow ，默认是""表示不需要持续时间参数
         -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
        duration : 0 ,
        /*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
         切换到某帧后的接口事件，这个方法将切换到的frame的索引值当做唯一参数
         -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
        onSwap : null ,
        /*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
        窗口拉动滚动条的时候是否需要将tabsHandle变成fixed
        -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
        fixedWhenScroll : true
    } ;
})(jQuery) ;